import PuzzleGame from './puzzleGame';
import images from './getRandomImg';
import { error } from '@pnotify/core/dist/PNotify.js';
import '@pnotify/core/dist/PNotify.css';
import '@pnotify/core/dist/BrightTheme.css';

const refs = {
  sectionDifficult: document.querySelector('.js-difficult'),
  sectionPuzzle: document.querySelector('.js-puzzle-section'),
  selectForm: document.querySelector('.js-form-select'),
  selectButtons: document.querySelector('.js-select-buttons'),
  confirmButton: document.querySelector('.js-confirm-btn'),
  exampleImage: document.querySelector('.js-example-img'),
  backToMenuBtn: document.querySelector('button[data-type="menu"]'),
  restartBtn: document.querySelector('button[data-type="restart"]'),
  puzzleBox: document.querySelector('#box'),
};
let dragSrcEl = null;
let exampleImage = null;
// let currentPuzzle = null;

refs.selectForm.addEventListener('click', handleFormSubmit);
refs.selectButtons.addEventListener('click', handleSelectDifficult);
refs.backToMenuBtn.addEventListener('click', handleBackToMenuClick);
// refs.restartBtn.addEventListener('click', handleRestartBtnClick);

function handleFormSubmit(event) {
  if (event.target.type !== 'submit') {
    return;
  }

  if (event.target.dataset.selected === '') {
    error({
      title: 'Select difficulty!',
    });
    return;
  }

  if (event.target.dataset.selected === 'easy') {
    startPuzzle(3, 3);
  }
  if (event.target.dataset.selected === 'medium') {
    startPuzzle(4, 4);
  }
  if (event.target.dataset.selected === 'hard') {
    startPuzzle(5, 5);
  }
}

function startPuzzle(col, row) {
  const randomImage = images[Math.floor(Math.random() * (9 - 0)) + 0];
  exampleImage = randomImage;
  openPuzzle();
  currentPuzzle = new PuzzleGame(col, row, '#box', randomImage).startGame();
  initialDrag();
  console.log(currentPuzzle);
}

function openPuzzle() {
  refs.sectionDifficult.classList.add('none');
  refs.sectionPuzzle.classList.remove('none');
  refs.exampleImage.src = exampleImage;
}

function closePuzzle() {
  refs.sectionDifficult.classList.remove('none');
  refs.sectionPuzzle.classList.add('none');
  refs.exampleImage.src = '';
  refs.puzzleBox.innerHTML = '';
}

function handleBackToMenuClick() {
  closePuzzle();
}

// function handleRestartBtnClick() {
  
// }

// drag & drop functions
function initialDrag() {
  [].forEach.call(document.querySelectorAll('.puzzle-part'), function (item) {
    item.addEventListener('dragstart', handleDragStart);
  });
}

function handleSelectDifficult(event) {
  if (event.target.nodeName !== 'BUTTON') {
    return;
  }

  if (document.querySelector('.selected')) {
    const selectedButton = document.querySelector('.selected');
    selectedButton.classList.remove('selected');
  }
  event.target.classList.add('selected');
  refs.confirmButton.dataset.selected = event.target.dataset.difficult;
}

function handleDragStart(e) {
  [].forEach.call(document.querySelectorAll('.puzzle-part'), function (item) {
    item.addEventListener('dragenter', handleDragEnter);
    item.addEventListener('dragover', handleDragOver);
    item.addEventListener('dragleave', handleDragLeave);
    item.addEventListener('drop', handleDrop);
    item.addEventListener('dragend', handleDragEnd);
  });
  e.target.style.opacity = 0.4;

  dragSrcEl = e.target;
  e.dataTransfer.effectAllowed = 'move';
  e.dataTransfer.setData('text/html', e.target.outerHTML);
}

function handleDragOver(e) {
  if (e.preventDefault) {
    e.preventDefault();
  }

  e.dataTransfer.dropEffect = 'move';
}

function handleDragEnter(e) {
  e.target.classList.add('over');
}

function handleDragLeave(e) {
  e.target.classList.remove('over');
}

function handleDrop(e) {
  if (e.stopPropagation) {
    e.stopPropagation();
  }

  if (dragSrcEl !== e.target) {
    dragSrcEl.outerHTML = e.target.outerHTML;
    e.target.outerHTML = e.dataTransfer.getData('text/html');
  }
}

function handleDragEnd() {
  Array.from(document.querySelectorAll('.puzzle-part')).forEach(item => {
    item.addEventListener('dragstart', handleDragStart, false);
    item.style.opacity = 1;
    item.classList.remove('over');
  });
  Array.from(document.querySelectorAll('.puzzle-part')).forEach(item => {
    item.removeEventListener('dragenter', handleDragEnter);
    item.removeEventListener('dragover', handleDragOver);
    item.removeEventListener('dragleave', handleDragLeave);
    item.removeEventListener('drop', handleDrop);
    item.removeEventListener('dragend', handleDragEnd);
  });
}
