export default class PuzzleGame {
  constructor(col, row, container, imgUrl) {
    this.col = col;
    this.row = row;
    this.container = document.querySelector(container);
    this.imgUrl = imgUrl;
    this.boxesArray = [];
    this.containerWidth = this.container.clientWidth;
    this.containerHeight = this.container.clientHeight;
  }

  createBoxes() {
    const quantityOfBoxes = this.col * this.row;
    const boxWidth = this.containerWidth / this.col;
    const boxHeight = this.containerHeight / this.row;

    for (let i = 0; i < quantityOfBoxes; i += 1) {
        const dataRow = -1 + (i + 1) - Math.floor(i / this.col) * this.col;
        const dataCol = Math.floor( i / this.col);
      const box = `<div class="puzzle-part" draggable="true"  style="width: ${boxWidth}px; height: ${boxHeight}px; background-image: url(${
        this.imgUrl
      }); background-position:${boxWidth * -dataRow}px ${boxHeight * -dataCol}px"></div>`;
      this.boxesArray.push(box);
    }
    this.shuffleImage(this.boxesArray);
  }

  shuffleImage(array){
    array.sort(() => -.5 + Math.random());
    this.container.insertAdjacentHTML('beforeend', array.join(' '));
  }

  startGame() {
    this.createBoxes();
  }

  restartGame(){
    this.shuffleImage(this.boxesArray);
  }
}



